import React, { Component } from "react";
import Head from "next/head";
import Page from "../layouts/main";
import PageHeader from "../components/PageHeader/PageHeader";
import data from '../static/json/data.json';

class Testimonials extends Component {
  
  constructor(props) {
    super(props);
    this.data = data.testimonials.page && data.testimonials.page.length > 0 ? data.testimonials.page : [];
    this.state = {
      loaded: this.data.length < 5 ? this.data.length : 5
    }
  }

  componentDidMount() {
    window.addEventListener('scroll', this.loadMore);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.loadMore);
  }

  loadMore = () => {
    if (this.LoadTrigger.getBoundingClientRect().top < window.innerHeight) {
      if (this.state.loaded < this.data.length - 1) this.setState({ loaded: this.state.loaded + 1 })
    }
  }

  render() {
    return (
      <Page id="Testimonials" menu={{solidStyle: true}}>
        <PageHeader
          title="Testimonials"
          image='testimonials.jpg'
          description='Hospall Private Homecare is always accepting employment applications from individuals dedicated to providing high-quality, in-home care assistance. We offer home care jobs and other employment opportunities to those who share our commitment and uphold our standards.' />
        <div className="ContentContainer">
          <div className="col-12 col-md-10 col-lg-9 col-xl-10">
            { this.data.slice(0, this.state.loaded).map((item, index) => {
                var patt = /\./igm;
                let match, count = 0, position = 0;
                while (match = patt.exec(item.content) && count < 2) {
                  position = patt.lastIndex;
                  count++;
                }
                return (
                  <span key={index} ref={el => { if (index === this.state.loaded - 1) this.LoadTrigger = el }}>
                    <h5 className="dark padding">{ item.content.substr(0, position) }</h5>
                    <p className="dark mini">{ item.content }</p>
                    <p className="dark mini padding">â€” { item.client }</p>
                  </span>
                )}
              )
            }
            <p>DISCLMAIMER: NAMES OF CLIENTS HAVE BEEN REPLACED TO PROTECT THEIR PRIVACY.</p>
          </div>
        </div>
      </Page>
    )
  }
}

export default Testimonials
